import React, { Component } from 'react';

class TimerButton extends Component {
  render() {
    return (
      <div>
        <button className="submit-button bttn"
          onClick={this.props.onClick}
          type="button"
        >
          {this.props.name}
        </button>
      </div>
    );
  }
}

export default TimerButton;


